/* Mixin */

.mobileModal {
  composes: scrollbars from '../../Sass/common/_base.scss';
  position: fixed;
  top: 50px;
  bottom: 0;
  background: rgba(255, 255, 255, 0.9);
  left: 0;
  right: 0;
  color: #000;
  display: none;
  overflow-x: hidden;
  overflow-y: hidden;
  width: 100%;
}

.mobileModal.isOpen {
  display: block;
}

