/* Mixin */

.btn-download {
  composes: btn from '../../../Sass/common/_buttons.scss';
  padding: 10px;
  color: #fff;
  float: right;
  margin-right: 40px;
}

.btn-download svg {
  height: 20px;
  width: 20px;
  fill: #ffffff;
  display: inline-block;
  vertical-align: bottom;
}

.btn-download:hover,
.btn-download:focus {
  text-decoration: none;
  color: #08ABD5;
}

.btn-download:before {
  display: inline-block;
}




