.divider-wrapper {
  position: absolute;
  width: 100%;
  height: 100%;
}

.divider {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 50%;
  width: 1px;
  background-color: #fff;
  pointer-events: none;
  z-index: 999;
}

.thumb {
  position: absolute;
  width: 40px;
  height: 40px;
  border-radius: 20px;
  border: 1px solid lightgrey;
  text-align: center;
  line-height: 38px;
  left: 50%;
  top: 50%;
  font-size: 28px;
  font-family: monospace;
  background-color: white;
  color: grey;
  margin-top: -20px;
  margin-left: -20px;
  z-index: 999;
  padding: 8px;
  cursor: ew-resize;
}

.thumb svg {
  fill: #000;
}




